import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { DataTransferService } from 'src/app/services/data-transfer.service';

@Component({
  selector: 'app-qualification',
  templateUrl: './qualification.page.html',
  styleUrls: ['./qualification.page.scss'],
})
export class QualificationPage implements OnInit {
  homePageData: any;

  constructor(private router:Router,private sendData:DataTransferService) { 
    this.homePageData=this.sendData.alldata;
  }

  ngOnInit() {
  }
  gotoNext(val){
    this.sendData.myParam=val;
    this.sendData.alldata=this.homePageData;
    this.router.navigate(['/qualification-form'])
  }

}
